<template>
  <div>
    <v-row>
      <v-col md="12" cols="12">
        <BlockItem>
          <span slot="title">نوع محاسبه قیمت</span>
          <template slot="icon">mdi-google-pages</template>

          <v-form ref="form" >
            <v-row v-if="renderComponent" >
              <v-col
                  cols="12"
                  md="3"
              >
                <v-text-field
                    outlined
                    :rules="[$store.state.rules.required(title,'عنوان')]"
                    v-model="title"
                    label="عنوان"
                    dense
                    color="txtf"
                >

                </v-text-field>
              </v-col>

              <v-col
                  cols="12"
                  md="3"
              >
                <v-file-input
                    outlined
                    :rules="[$store.state.rules.required(image,'تصویر')]"
                    v-model="image"
                    label="تصویر"
                    dense
                    color="txtf"
                >

                </v-file-input>
              </v-col>

              <v-col
                  cols="12"
                  md="2"
              >
                <v-btn v-if="!id" dark class="dash-btns dash-btns--blue" :disabled="loading" @click="addCategory()">
                  + افزودن
                </v-btn>
                <!--                <v-btn v-else dark color="orange darken-2" :disabled="loading" @click="editCategory()">
                                  ویرایش
                                </v-btn>-->
                <v-btn text dark v-if="id" @click="editCategory()" color="#17a2b8" >
                  <v-icon  color="#17a2b8" :disabled="loading"  >mdi-check</v-icon>
                  ویرایش
                </v-btn>
                <v-btn small height="36" text dark v-if="id" @click="cleanEdit()" color="red" >
                  <v-icon  color="error" :disabled="loading"  >mdi-close</v-icon>
                </v-btn>
              </v-col>
            </v-row>
          </v-form>

          <v-data-table
              hide-default-footer
              :headers="headers" :loading="loading" :items="data" item-key="id" disable-sort
          >

            <template v-slot:[`item.enabled`]="{item}">
              <span class="table-flag-enabled table-flag-enabled--green" v-if="item.enabled" >فعال</span>
              <span class="table-flag-enabled table-flag-enabled--red" v-else>غیر فعال</span>
            </template>

            <template v-slot:[`item.func`]="{item}">
              <div class="d-flex justify-center">
                <a-sbtn @clickBtn="toEdit(item)" color="#17a2b8" icon="mdi-pencil" alt="ویرایش کردن" ></a-sbtn>
                <a-sbtn @clickBtn="toDelete(item.id,'/pricing/api/v1/delete-pricing-type/')" color="#DC3545" icon="mdi-delete" alt="حذف کردن" ></a-sbtn>
              </div>
            </template>


          </v-data-table>
        </BlockItem>
      </v-col>
      <v-col md="12" cols="12">
        <BlockItem>
          <span slot="title">اطلاعات مشتریان محاسبه</span>
          <template slot="icon">mdi-account-search</template>
          <v-data-table
              hide-default-footer
              :headers="PricingHeaders" :loading="loading" :items="pricingData" item-key="id" disable-sort
          >

            <template v-slot:[`item.action`]="{item}">
              <div class="d-flex justify-center">
                <a-sbtn @clickBtn="toDelete(item.id,'/pricing/api/v1/delete-pricing/')" color="#DC3545" icon="mdi-delete" alt="حذف کردن" ></a-sbtn>
              </div>
            </template>


          </v-data-table>
        </BlockItem>
      </v-col>
    </v-row>
    <DeleteDialog :ddata="deleteData" @deleteFn="fetchData()"></DeleteDialog>
  </div>
</template>

<script>
import axios from "axios";
import BlockItem from "@/components/Misc/BlockItem";
import DeleteDialog from "@/components/Misc/DeleteDialog";
import {forceRerender} from "@/components/Misc/forceRerender";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";

export default {
  name: "DashPricing",
  mixins : [forceRerender],
  components: {
    ASbtn,
    DeleteDialog,
    BlockItem
  },
  created() {
    this.fetchData();
  },
  data() {
    return {
      pricingData : [],
      id: null,
      enabled: true,
      name: null,
      image: null,
      title: null,
      options: {},
      loading: false,
      data: [],
      deleteData: {
        api: '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
      commentData: {
        dialog: false,
        loading: false,
        idSelected: null,
      }
    }
  },
  computed: {
    headers() {
      return [
        {
          text: 'شماره',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'id',
        },
        {
          text: 'عنوان',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'title',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'func',
        },


      ]
    },
    PricingHeaders() {
      return [
        {
          text: 'شماره',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'id',
        },
        {
          text: 'نام شخص',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'full_name',
        },
        {
          text: 'همراه',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'phone_number',
        },{
          text: 'زیر بنا',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'total_infrastructure',
        },{
          text: 'تعداد تیپ پلان',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'brigade_plans_num',
        },{
          text: 'واحد در هر تیپ',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'units_brigade',
        },{
          text: 'تعداد وجه نما',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'facades_num',
        },{
          text: 'محوطه و روف گاردن',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'yard_and_roof_garden_meterage',
        },
        {
          text: 'تعداد رندر',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'render_num',
        },
        {
          text: 'مساحت طراحی داخلی',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'interior_design_area',
        },
        {
          text: 'رندر طراحی داخلی',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'interior_design_area',
        },{
          text: 'مدت روز',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'project_duration',
        },
        {
          text: 'نتیجه',
          align: 'end',
          class: 'header-table-gray',
          sortable: false,
          value: 'final_result',
        },
        {
          text: 'عملیات',
          align: 'end',
          class: 'header-table-gray',
          sortable: false,
          value: 'action',
        },
      ]
    },
  },
  methods: {
    fetchData() {
      this.fetchCategories();
      this.fetchPircing();
    },
    addCategory() {

      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        if(this.image.size>2097152) {
          this.notify('حجم عکس بیشتر از 2 م.گ','error')
          return
        }
        if((this.image.type!=='image/jpeg') && (this.image.type!=='image/jpg') && (this.image.type!=='image/png')) {
          this.notify('فرمت غیر قابل پشتیبانی','error')
          return
        }
        let formData = new FormData();

        formData.append('title',this.title)

        if (this.image && (typeof this.image === 'object')) {
          formData.append('image', this.image);
        }
        this.id= null;
        this.title = null;
        this.image = null;
        this.loading = true;
        this.forceRerender();
        axios.post(this.$store.state.host + '/pricing/api/v1/create-pricing-type', formData, {

          headers: {
            'Content-Type': 'multipart/form-data',
            Accept: "*/*",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },

        }).then(() => {
          this.notify('نوع محاسبه افزوده شد', 'info');
          this.fetchCategories();
        }).catch(error => {
          this.errorHandle(error.response)
        })
      }
    },
    editCategory() {

      this.$refs.form.validate();
      if (this.$refs.form.validate()) {

        if(this.image.size>2097152) {
          this.notify('حجم عکس بیشتر از 2 م.گ','error')
          return
        }
        if((this.image.type!=='image/jpeg') && (this.image.type!=='image/jpg') && (this.image.type!=='image/png')) {
          this.notify('فرمت غیر قابل پشتیبانی','error')
          return
        }
        let formData = new FormData();

        formData.append('title',this.title)

        if (this.image && (typeof this.image === 'object')) {
          formData.append('image', this.image);
        }

        const id = this.id;
        this.id= null;
        this.title = null;
        this.image = null;
        this.loading = true;
        this.forceRerender();
        axios.patch(this.$store.state.host + '/pricing/api/v1/update-pricing-type/'+id, formData, {

          headers: {
            'Content-Type': 'multipart/form-data',
            Accept: "*/*",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },

        }).then(() => {
          this.notify('نوع محاسبه ویرایش شد', 'warning');
          this.fetchCategories();
        }).catch(error => {
          this.errorHandle(error.response)
        })
      }
    },
    toEdit(item) {
      this.id = item.id;
      this.title = item.title;
    },
    cleanEdit(){
      this.id = null;
      this.image = null;
      this.title = null;
      this.forceRerender();
    },
    toDelete(id, api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },

    fetchCategories() {
      this.loading = true;
      axios.get(this.$store.state.host + '/pricing/api/v1/pricing-types', {
        params: this.options
      }).then(response => {
        this.data = response.data.data;
        this.loading = false;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    },

    fetchPircing() {
      axios.get(this.$store.state.host + '/pricing/api/v1/pricings',{
        headers : {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(response => {
        this.pricingData = response.data.data;
        this.loading = false;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    }
  }
}
</script>

